@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro);

.main-container { 
	width: 100vw;
	height: 100vh;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
  }
  
  .title {
    width: 13em;
    margin: 0 auto;
    border-right: 2px solid rgba(255,255,255,.75);
    font-size: 2em;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);    
  }
  
  /* Animation */
.title--animation {
	-webkit-animation: typer 3s steps(44) 1s 1 normal both,
			   cursor 500ms steps(44) infinite normal;
	        animation: typer 3s steps(44) 1s 1 normal both,
			   cursor 500ms steps(44) infinite normal;
}
@-webkit-keyframes typer {
	from {width: 0;}
	to {width: 13em;}
}
@keyframes typer {
	from {width: 0;}
	to {width: 13em;}
}

@-webkit-keyframes cursor {
	from {border-right-color: rgba(255,255,255,.75);}
	to {border-right-color: transparent;}
}

@keyframes cursor {
	from {border-right-color: rgba(255,255,255,.75);}
	to {border-right-color: transparent;}
}


